<template>
    <div class="page">
        <div class="wrap">
            <div class="tab_wrap">
                <el-radio-group v-if="showType != 'table'" v-model="tabPosition" size="mini">
                    <el-radio-button  label="map">地图</el-radio-button>
                    <el-radio-button label="table">列表</el-radio-button>
                </el-radio-group>
                <el-radio-group v-else v-model="tabPosition" size="mini">
                    <el-radio-button label="table">列表</el-radio-button>
                </el-radio-group>
            </div>
            <div v-show="tabPosition == 'map'" class="map_wrap">
                <div id="boxMap" v-loading="loading"></div>
            </div>
            <div v-if="showType == 'table'" v-show="tabPosition =='table'" class="table_wrap">
                <el-table
                    :data="tableData"
                    style="width: 100%"
                    :header-cell-style="{background:'#f2f2f2',color:'#606266'}"
                >
                    <el-table-column prop="date" label="日期"></el-table-column>
                    <el-table-column prop="name" label="姓名"></el-table-column>
                    <el-table-column prop="address" label="地址"></el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tabPosition: "map",
            tableData: []
        };
    },
    props: {
        showType: {
            type: String,
            required: false
        }
    },
    created() {
        if (this.showType == "table") {
            this.tabPosition = 'table';
        }
    },
    mounted() {
        this.getMapData();
    },
    methods: {
        getMapData() {
            let vm = this;
            if (this.showType != "table") {
                vm.setMap();
            }
        },
        /**
         * 初始化地图
         */
        setMap(data) {
            let point = new BMap.Point(116.404, 39.915);
            //初始化地图

            let map = new BMap.Map("boxMap");

            let vm = this;
            vm.mapObj = map;
            var overView = new BMap.OverviewMapControl();
            var overViewOpen = new BMap.OverviewMapControl({
                isOpen: true,
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT
            });
            map.setMinZoom(3); //最小zoom缩放级别
            map.setMaxZoom(14); //最大zoom缩放级别
            // map.disableScrollWheelZoom(); //关闭鼠标滚轮缩放，自定义滚轮事件，调用map.zoomIn()与map.zoomOut()来控制缩放，避免直接使用而导致一次放大或缩小几级导致数据加载问题
            // vm.bindZoomSwithListener(map); //初始化zoom缩放监听事件
            map.enableScrollWheelZoom();
            // vm.bindProvinceAndCitySwitchListener(map); //初始化map拖动监听事件
            map.addControl(overView); //添加默认缩略地图控件
            map.addControl(overViewOpen);
            map.centerAndZoom(point, 11);
        }
    }
};
</script>

<style scoped lang='scss'>
.red {
    background: red;
}
.wrap {
    padding: 12px;
}
.tab_wrap {
    padding-bottom: 10px;
    border-bottom: 1px solid #e4ebf0;
}
.map_wrap {
    margin-top: 14px;
    width: 100%;
    height: 700px;
    overflow: hidden;
    position: relative;
    z-index: 0;
    background-color: rgb(243, 241, 236);
    color: rgb(0, 0, 0);
    text-align: left;
}
.page {
    width: 90%;
    min-height: 448px;
    margin-left: 10px;
    background: #fff;
}
#boxMap {
    height: 100%;
    width: 100%;
}
</style>



